import React, {useRef} from "react";
import tw from "twin.macro";

import AnimationRevealPage from "helpers/AnimationRevealPage.js";
import FAQ from "components/faqs/SingleCol.js";
import Footer from "components/sections/Footer.js";
import BackgroundAsImage from "../components/hero/BackgroundAsImage";
import OurServices from "components/features/OurServices";
import DownloadApp from "../components/cta/DownloadApp";
import SimpleSubscribeNewsletter from "../components/forms/SimpleSubscribeNewsletter";
import OurTeam from "../components/sections/OurTeam";
import OurValues from "../components/sections/OurValues";
import {Toaster} from 'react-hot-toast';

export default () => {
  const Subheading = tw.span`uppercase tracking-widest font-bold text-primary-500`;
  const HighlightedText = tw.span`text-primary-500`;

  const Divider = tw.div`my-8 border-b-2 border-gray-800`

  const servicesRef = useRef(null);
  const ourTeamRef = useRef(null);
  const ourValuesRef = useRef(null);
  const applicationRef = useRef(null);
  const faqsRef = useRef(null);
  const footerRef = useRef(null);

  return (<AnimationRevealPage disabled>
    <BackgroundAsImage
      contactUsSectionRef={footerRef}
      menusRef={[servicesRef, ourTeamRef, ourValuesRef, applicationRef, faqsRef]}
      heading={<>
        Vous êtes fournisseur de <HighlightedText>tickets wifi zone ?</HighlightedText>
      </>}
      description={<>
        ENFIN, Vos tickets wifi zone via T-money et Moov Money
      </>}
    />
    <OurServices
      sectionRef={servicesRef}
      heading={<>
        Wefree, qu'est ce que c'est ?
      </>}
      description={<>
        Wefree vous permet en tant que client d'acheter directement et librement
        vos tickets wifi zone en ligne via T-money et Moov money sans le moindre
        frais. Wefree fournit aux centres de wifi zone une interface de gestion
        de leurs routeurs et de suivi en temps réel des transactions.
      </>}
    />
    {/*<VideoSection heading={"Suivez notre vidéo d'introduction"} description={''}
                  watchVideoButtonText={'Lancer la lecture'}/>*/}
    <Divider/>
    <OurTeam sectionRef={ourTeamRef} subheading={''} heading={'Notre équipe'} description={<span>
        Notre savoir-faire et notre expérience en innovation technologique nous permettent de mettre en place
        des procédés basés sur des technologies de pointe et à jour. Notre ultime but est de vous fournir un service
        de qualité irréprochable afin de vous satisfaire, vous particulièrement.
        <br/><br/>
        Nos équipes techniques et commerciales sont composées de jeunes hommes et femmes chevronnés et débordés d’envie
        d’innovation et de découverte.
      </span>} primaryButtonText={'Rejoignez nous'} joinUsSectionRef={footerRef}/>
    <OurValues sectionRef={ourValuesRef}/>
    <DownloadApp sectionRef={applicationRef} subheading={'Téléchargez l’application'}
                 text={'Avec l’application mobile Wefree, profitez d\'un éventail plus large de fonctionnalités.'}/>
    <FAQ
      sectionRef={faqsRef}
      subheading={<Subheading>FAQS</Subheading>}
      heading={<>
        Nous répondons à vos <HighlightedText>questions</HighlightedText>.
      </>}
      description={''}
      faqs={[{
        question: "Comment être éligible pour intégrer Wefree à mon routeur ?",
        answer: "Tout centre de wifi zone ou toute personne qui désire vendre des tickets de connexion est éligible pour rendre disponible Wefree sur l’interface de connexion à son routeur."
      }, {
        question: "Comment mettre à disposition mes tickets ?",
        answer: "Créez rapidement votre compte sur mywefree.com et enregistrez votre routeur. Ensuite accédez à votre tableau de bord et suivez les instructions."
      }, {
        question: "Par quels moyens mes clients obtiendront les tickets ?",
        answer: "Mes clients peuvent payer les tickets via T-money ou Moov money directement sur l’interface de connexion à mon routeur et sur l’application mobile Wefree disponible en téléchargement sur Play store sans frais."
      }, {
        question: "Quels sont les frais engagés pour profiter de Wefree ?",
        answer: "Wefree est totalement gratuit et vous n’engagez aucun frais pour son intégration."
      }, {
        question: "Est-ce que je peux continuer par vendre mes tickets physiques ?",
        answer: "En parallèle à Wefree, vous pouvez naturellement continuer par vendre vos tickets physiques comme d'habitude."
      }]}
    />
    <SimpleSubscribeNewsletter/>
    <Footer menusRef={[servicesRef, ourTeamRef, ourValuesRef, applicationRef, faqsRef]} sectionRef={footerRef}/>
    <Toaster/>
  </AnimationRevealPage>);
}
